<template>
  <el-table :data="questionList" style="width: 100%">
    <el-table-column prop="id" label="ID" width="50" />
    <el-table-column prop="stem" label="Stem" width="100" />
    <el-table-column prop="weight" label="Weight" width="100" />
    <el-table-column prop="chosenTimes" label="ChosenTimes" width="150" />
    <el-table-column prop="totalScore" label="TotalScore" width="100" />
    <el-table-column prop="cheatTimes" label="CheatTimes" width="150" />
    <el-table-column prop="option1" label="Option1" width="100" />
    <el-table-column prop="option2" label="Option2" width="100" />
    <el-table-column prop="option3" label="Option3" width="100" />
    <el-table-column prop="option4" label="Option4" width="100" />
    <el-table-column prop="option5" label="Option5" width="100" />
    <el-table-column>
      <template #default="scope">
        <el-button type="warning" @click="showDialogEdit(scope.row)"
          >Edit</el-button
        >
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    class="pagination"
    :currentPage="pagination.currentPage"
    :page-size="pagination.pageSize"
    :total="pagination.total"
    @current-change="handleCurrentChange"
    layout="total, prev, pager, next, jumper"
  ></el-pagination>

  <el-dialog v-model="dialogEditVisible" title="Edit">
    <el-form :model="formData">
      <el-form-item label="Stem">
        <el-input autosize type="textarea" v-model="formData.stem"></el-input>
      </el-form-item>
      <el-form-item label="Weight">
        <el-input v-model="formData.weight"></el-input>
      </el-form-item>
      <el-form-item label="Option1">
        <el-input v-model="formData.option1"></el-input>
      </el-form-item>
      <el-form-item label="Option2">
        <el-input v-model="formData.option2"></el-input>
      </el-form-item>
      <el-form-item label="Option3">
        <el-input v-model="formData.option3"></el-input>
      </el-form-item>
      <el-form-item label="Option4">
        <el-input v-model="formData.option4"></el-input>
      </el-form-item>
      <el-form-item label="Option5">
        <el-input v-model="formData.option5"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">Cancel</el-button>
        <el-button type="primary" @click="checkDialogEdit">OK</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ElMessage } from "element-plus";

export default {
  data() {
    return {
      questionList: [],

      formData: {},
      dialogEditVisible: false,

      pagination: {
        currentPage: 1,
        pageSize: 10,
        total: 1,
      },
    };
  },
  created() {
    this.getPageByCondition();
  },
  methods: {
    getAll() {
      axios.get("/background/questions").then((res) => {
        if (res.data.code === 0) this.questionList = res.data.data;
        else ElMessage.error(res.data.msg);
      });
    },
    getPageByCondition() {
      axios
        .get(
          "/background/questions/" +
            this.pagination.currentPage +
            "/" +
            this.pagination.pageSize
        )
        .then((res) => {
          if (res.data.code === 0) {
            this.questionList = res.data.data.records;
            this.pagination.total = res.data.data.total;
            this.pagination.currentPage = res.data.data.current;
            this.pagination.pageSize = res.data.data.size;
          } else ElMessage.error(res.data.msg);
        });
    },
    cancel() {
      this.dialogEditVisible = false;
      this.formData = {};
    },
    showDialogEdit(row) {
      this.formData = {};
      axios
        .get("/background/questions/" + row.id)
        .then((res) => {
          if (res.data.code === 0) {
            this.formData = res.data.data;
            this.dialogEditVisible = true;
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    checkDialogEdit() {
      axios
        .put("/background/questions", this.formData)
        .then((res) => {
          if (res.data.code === 0) {
            this.dialogEditVisible = false;
            ElMessage.success(res.data.msg);
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    handleCurrentChange(currentPage) {
      this.pagination.currentPage = currentPage;
      this.getPageByCondition();
    },
  },
};
</script>

<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}

.pagination {
  margin-top: 10px;
  float: right;
}
</style>
